import * as React from 'react'
import Layout from '../components/layout'

const CrankyPage = () => {
  return (
    <Layout pageTitle="Art">
      // THIS IS A WORK IN PROGRESS PAGE 
      <p>Developed for <a href="https://itch.io/c/1740469/reverse-game-jam-summer-2021-less-is-more">ACM Studio Reverse Game Jam</a> during Summer 2021</p>

      <p><a href="https://docs.google.com/document/d/1ADAE1WaOkQqoeH7fi1AYCkFMrH93UgJzWDnY6B4s--k/edit">Cranky Script</a> to which I need to add more notes about time for people to understand better</p>
      <p>My first experience with game design was working on the comedic walking simulator "Cranky." through participating in my school club's "Reverse Game Jam", which had people take on unfamiliar roles in game development (which, for me, was anything other than 2D art). I worked with my team members to establish the game concept and wrote the dialogue for the game. I also included notes for where an interaction, animation, or sound effect would be appropriate and placed all interactables such that the dialogue and movement direction would naturally lead the player to them in the intended order. I compensated for features we didn't implement in time through the dialogue, and I'm proud that our game was narratively complete in the end! I felt more satisfied finishing "Cranky." than any of the previous games I'd worked on, even though those other games were more polished, because figuring out how to create the experience I wanted players to have was a fun puzzle for me to solve.</p>
    </Layout>
  )
}
export default CrankyPage